@font-face {
  src: url("font/GalanoGrotesqueAltRegular.0e190648.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesqueAltSemiBold.710dbe2b.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Bold.2be9a942.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Black.d7ff6920.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

:root {
  --font-family-primary: "GalanoGrotesqueAlt", sans-serif;
  --font-family-secondary: "GalanoGrotesque", sans-serif;
  --content-width: 1110px;
  --content-wide: 1742px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --container-wide: calc(var(--content-wide)  + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-dark-400: #1a1a1a;
  --color-gray-50: #f1f2f6;
  --color-gray-100: #b5b5b5;
  --color-gray-150: #dadada;
  --color-blue-400: #0596e8;
  --color-red-100: #fee;
  --color-gradient-main: linear-gradient(180deg, #80d0c8 0%, #0596e8 100%);
  --color-gradient-gray: linear-gradient(0deg, #ffffff4d 0%, #c3cfe24d 100%);
  --color-gradient-border: linear-gradient(var(--rotate), #dadada00 0%, #dadada 47.4%, #dadada00 100%);
  --color-aside: linear-gradient(0deg, #f5f7fa4d -49.83%, #c3cfe24d 100%);
  --shadow-ligth: 0 4px 14px #0000001a;
  --radius-main: 10px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

.page {
  height: 100%;
  font-family: var(--font-family-primary);
  letter-spacing: -.03em;
  color: var(--color-dark-400);
  background-color: var(--color-white);
  scroll-padding-top: var(--header-height, 80px);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
  overflow: hidden;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
}

@media only screen and (max-width: 576px) {
  .page {
    letter-spacing: normal;
    font-size: 14px;
    line-height: 1.14286;
  }
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.container--wide {
  max-width: var(--container-wide);
}

.main {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
}

.main .btn {
  letter-spacing: normal;
  font-size: 18px;
  line-height: 1.22222;
}

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.dis-scroll {
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.text-gradient {
  font-family: var(--font-family-secondary);
  color: #0000;
  background: var(--color-gradient-main);
  -webkit-background-clip: text;
  background-clip: text;
}

.tooltip {
  letter-spacing: normal;
  box-shadow: var(--shadow-ligth);
  background-color: var(--color-white);
  opacity: 0;
  visibility: hidden;
  border-radius: 5px;
  padding: 5px 8px 5px 20px;
  font-size: 10px;
  line-height: 1.2;
  transition-property: opacity, visibility;
  transition-duration: .1s;
  position: absolute;
}

.no-result {
  letter-spacing: normal;
  text-align: center;
  font-size: 14px;
  line-height: 1.35714;
}

.nickname {
  font-family: var(--font-family-secondary);
  font-weight: 700;
}

.copy.copied + .tooltip {
  opacity: 1;
  visibility: visible;
}

h1 {
  text-align: center;
}

.title {
  letter-spacing: normal;
  font-size: 32px;
  line-height: 1.1875;
  font-family: var(--font-family-secondary);
  font-weight: 900;
}

@media only screen and (max-width: 1280px) {
  .title {
    letter-spacing: normal;
    font-size: 25px;
    line-height: 1;
  }
}

.title span {
  font-family: var(--font-family-secondary);
  color: #0000;
  background: var(--color-gradient-main);
  -webkit-background-clip: text;
  background-clip: text;
  display: inline-block;
}

.title--secondary {
  letter-spacing: normal;
  font-size: 24px;
  line-height: 1.16667;
}

@media only screen and (max-width: 1280px) {
  .title--secondary {
    letter-spacing: normal;
    font-size: 18px;
    line-height: 1.16667;
  }
}

.title--small {
  letter-spacing: normal;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.05556;
}

.numbers {
  justify-content: space-between;
  margin-bottom: 27px;
  display: flex;
}

.numbers__item {
  width: 100%;
  max-width: 145px;
  padding: 5px 0 22px;
  position: relative;
}

.numbers__item:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--color-gray-50);
  position: absolute;
  bottom: 0;
}

.numbers__item:after {
  content: "";
  width: 50px;
  height: 50px;
  background: var(--color-gradient-main);
  opacity: .1;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.numbers__item > * {
  display: block;
}

.numbers__image {
  margin-top: 4px;
  margin-bottom: 1.6875rem;
  margin-left: 8px;
}

.numbers__number {
  letter-spacing: normal;
  margin-bottom: 7px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.16667;
}

.numbers__desc {
  letter-spacing: normal;
  opacity: .5;
  font-size: 14px;
  line-height: 1.14286;
}

.deposit {
  border-radius: calc(var(--radius-main) * 2.5);
  width: 100%;
  background: var(--color-aside);
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap: 35px;
  margin-bottom: 28px;
  padding: 14px;
  display: grid;
}

.deposit__item {
  text-align: center;
  flex-direction: column;
  display: flex;
  position: relative;
}

.deposit__item:not(:last-child) {
  --rotate: 180deg;
}

.deposit__item:not(:last-child):after {
  content: "";
  width: 1px;
  height: calc(100% + 28px);
  background: linear-gradient(var(--rotate), #dadada00 0%, #dadada 47.4%, #dadada00 100%);
  position: absolute;
  top: -14px;
  right: -19px;
}

.deposit__number {
  letter-spacing: normal;
  font-size: 24px;
  line-height: 1.16667;
  font-family: var(--font-family-secondary);
  font-weight: 900;
}

@media only screen and (max-width: 1180px) {
  .deposit__number {
    letter-spacing: normal;
    font-size: 19px;
    line-height: 1.26316;
  }
}

.referal {
  border-radius: calc(var(--radius-main) * 2.5);
  width: 100%;
  background: var(--color-aside);
  padding: 21px 1.875rem 21px 1.5625rem;
}

.referal__link {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  display: flex;
}

@media only screen and (max-width: 1180px) {
  .referal__link {
    display: block;
  }

  .referal__title {
    margin-bottom: 15px;
  }
}

.referal__item {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.referal__item:not(:last-child) {
  margin-bottom: 9px;
}

.referal__copy {
  letter-spacing: normal;
  border-radius: var(--radius-main);
  width: 100%;
  max-width: 516px;
  border: 1px solid #1a1a1a1a;
  align-items: center;
  padding-right: 10px;
  font-size: 14px;
  line-height: 1.14286;
  display: flex;
  position: relative;
}

.referal__copy-link {
  word-break: break-all;
  padding: 16px 25px 16px 20px;
  display: block;
}

.referal__number {
  letter-spacing: normal;
  width: 100%;
  max-width: 50px;
  font-size: 18px;
  line-height: 1.16667;
  font-family: var(--font-family-secondary);
  text-align: left;
  font-weight: 900;
}

.referal__copy-btn {
  width: 25px;
  height: 25px;
  position: relative;
}

.referal__copy-btn:before {
  content: "";
  width: 22px;
  height: 22px;
  background-image: url("icons/copy.43deaaaf.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: -1px;
  left: -1px;
}

.referal__tooltip {
  top: -18px;
  right: -6px;
}

.referal__tooltip:before {
  content: "";
  width: 9px;
  height: 9px;
  background-image: url("icons/success-check.7488f839.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  left: 8px;
}

.referal__list {
  width: 100%;
  max-width: 320px;
}

.own__info {
  align-items: center;
  margin-top: -3px;
  margin-bottom: 2.0625rem;
  display: flex;
}

.own__title {
  letter-spacing: normal;
  margin-right: 18px;
  font-size: 24px;
  line-height: 1;
}

.own__link {
  border: 1px solid var(--color-blue-400);
  border-radius: var(--radius-main);
  width: 100%;
  max-width: 150px;
  justify-content: center;
  align-items: center;
  padding: 12px;
  display: flex;
  position: relative;
}

.own__link:before {
  content: "";
  width: 22px;
  height: 22px;
  background-image: url("icons/money.595f3a0a.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(-50%);
}

.own__link > span {
  font-family: var(--font-family-secondary);
  margin-left: 1.5625rem;
}

.own__text {
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  display: flex;
  position: relative;
}

.own__text:before {
  content: "";
  z-index: -1;
  width: calc(100% + 56px);
  height: 1px;
  background-color: var(--color-gray-150);
  position: absolute;
  top: 50%;
  left: -28px;
}

.own__text span {
  letter-spacing: normal;
  font-size: 18px;
  line-height: .944444;
  font-family: var(--font-family-secondary);
  letter-spacing: -.04em;
  background-color: var(--color-white);
  padding: 15px 15px 15px 10px;
  font-weight: 900;
}

.cryptomaker {
  border-radius: var(--radius-main);
  box-shadow: var(--shadow-ligth);
  padding: 3px 20px 14px 18px;
}

.cryptomaker__main {
  --rotate: 280deg;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.3125rem;
  display: flex;
  position: relative;
}

.cryptomaker__main:after {
  content: "";
  width: 100%;
  height: 1px;
  background: linear-gradient(var(--rotate), #dadada00 0%, #dadada 47.4%, #dadada00 100%);
  margin: 0 auto;
  position: absolute;
  bottom: -20px;
  left: 0;
  right: 0;
}

.cryptomaker__desc {
  padding-top: 12px;
  padding-right: 10px;
}

.cryptomaker__title {
  letter-spacing: normal;
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.1875;
}

.cryptomaker__item {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.cryptomaker__item:not(:last-child) {
  margin-bottom: 13px;
}

.cryptomaker__icon {
  position: relative;
}

.cryptomaker__icon:before {
  font-family: var(--font-family-secondary);
  color: #0000;
  background: var(--color-gradient-main);
  letter-spacing: normal;
  content: attr(data-number);
  z-index: 1;
  width: 62px;
  height: 62px;
  font-size: 18px;
  line-height: 1.16667;
  font-family: var(--font-family-secondary);
  -webkit-background-clip: text;
  background-clip: text;
  justify-content: center;
  align-items: center;
  font-weight: 900;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
}

.cryptomaker__icon:after {
  content: "";
  width: 62px;
  height: 62px;
  background-color: #edf1f6;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
}

.money {
  margin-top: -5px;
}

.money__title {
  margin-bottom: 16px;
}

.money__subtitle {
  letter-spacing: -.03em;
  margin-bottom: 1.625rem;
  display: block;
}

.money__list {
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 1.875rem 1.625rem;
  margin-bottom: 2.6875rem;
  display: grid;
}

.money__radio {
  width: 100%;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.money__radio-content {
  border-radius: var(--radius-main);
  width: 100%;
  min-height: 120px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  transition: box-shadow .2s;
  display: flex;
  position: relative;
}

.money__radio-content:before {
  content: "";
  -webkit-mask-composite: destination-out;
  border-radius: var(--radius-btn);
  background: var(--color-gradient-main) border-box;
  z-index: -1;
  border-radius: var(--radius-main);
  border: 1px solid #0000;
  position: absolute;
  inset: 0;
  -webkit-mask-image: linear-gradient(#fff 0 0), linear-gradient(#fff 0 0);
  mask-image: linear-gradient(#fff 0 0), linear-gradient(#fff 0 0);
  -webkit-mask-position: 0 0, 0 0;
  mask-position: 0 0, 0 0;
  -webkit-mask-size: auto, auto;
  mask-size: auto, auto;
  -webkit-mask-repeat: repeat, repeat;
  mask-repeat: repeat, repeat;
  -webkit-mask-clip: padding-box, border-box;
  mask-clip: padding-box, border-box;
  -webkit-mask-origin: padding-box, border-box;
  mask-origin: padding-box, border-box;
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  -webkit-mask-source-type: auto, auto;
  mask-mode: match-source, match-source;
}

.money__radio-content span {
  font-family: var(--font-family-secondary);
  font-weight: 900;
}

.money__radio-content + span {
  letter-spacing: normal;
  color: #1a1a1a80;
  font-size: 14px;
  line-height: 1.14286;
}

.money__radio-content:hover, .money__item input:checked + .money__radio > div {
  box-shadow: 0 0 4px #0000000a, 0 8px 16px #00000014;
}

.money__item--paycore .money__image {
  margin-bottom: 15px;
}

.money__item--tether .money__image {
  max-width: 145px;
}

.money__image {
  width: 100%;
  max-width: 170px;
  margin-bottom: 5px;
}

.money__form {
  border-radius: calc(var(--radius-main) * 2.5);
  background: var(--color-gradient-gray);
  padding: 1.8125rem 2.25rem;
}

.money__form .money__subtitle {
  margin-bottom: 10px;
}

.money__withdraw {
  border: 1px solid var(--color-gray-100);
  border-radius: var(--radius-main);
  text-align: center;
  margin-bottom: 1.625rem;
  padding: 17px 33px;
  display: block;
}

.money__withdraw::placeholder {
  color: var(--color-dark-400);
}

.money__form-btn.btn {
  letter-spacing: normal;
  max-width: 260px;
  padding: 20px;
  font-size: 20px;
  line-height: 1;
}

.history {
  margin-top: -5px;
}

.history__title {
  margin-bottom: 1.4375rem;
}

.history .table th {
  letter-spacing: -.03em;
}

.history .table th:first-child {
  justify-content: flex-start;
  padding-left: 1.875rem;
}

.history .table td {
  vertical-align: top;
  padding: 17px 0;
}

.history .table td:first-child {
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 1.6875rem;
}

.history .table td:last-child {
  padding-right: 15px;
}

.history .table td:nth-child(2) img {
  margin-right: 10px;
  display: inline-block;
}

.history .table td:nth-child(2) span {
  display: inline-flex;
  transform: translateY(-10px);
}

.history .table p {
  letter-spacing: normal;
  font-size: 14px;
  line-height: 1.21429;
}

.account__title {
  margin-bottom: 1.5625rem;
}

.account__form {
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
  margin-bottom: 2.25rem;
  display: grid;
}

.account__fieldset {
  border: 1px solid var(--color-gray-100);
  border-radius: var(--radius-main);
  letter-spacing: -.03em;
  position: relative;
}

.account__fieldset:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 19px;
  transform: translateY(-50%);
}

.account__fieldset legend {
  letter-spacing: normal;
  text-transform: uppercase;
  background-color: var(--color-white);
  padding: 0 7px;
  font-size: 8px;
  line-height: 1.125;
  position: absolute;
  top: -6px;
  left: 13px;
}

.account__fieldset--user:before {
  width: 19px;
  height: 19px;
  background-image: url("icons/user.152d3939.svg");
}

.account__fieldset--date:before {
  width: 15px;
  height: 15px;
  background-image: url("icons/calendar.2f2958f2.svg");
}

.account__fieldset--email:before {
  width: 18px;
  height: 18px;
  background-image: url("icons/email.2d07b47c.svg");
}

.account__field {
  letter-spacing: normal;
  outline-color: var(--color-blue-400);
  border-radius: var(--radius-main);
  width: 100%;
  letter-spacing: -.03em;
  padding: 16px 20px;
  font-size: 16px;
  line-height: 1.1875;
}

.account__field:read-only {
  cursor: default;
  outline: none;
}

.account__password {
  width: 22px;
  height: 15px;
  background-color: #0000;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.account__password:before {
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("icons/eye.a6d40339.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  position: absolute;
  top: 0;
  left: 0;
}

.account__btn {
  width: 100%;
  max-width: 260px;
}

.codes {
  margin-bottom: 40px;
}

.codes__inner {
  justify-content: space-between;
  display: flex;
}

.codes__title {
  margin-bottom: 20px;
}

.codes__qr {
  border-radius: calc(var(--radius-main) * 2.5);
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 195px;
  background: var(--color-gradient-gray);
  justify-content: center;
  align-items: baseline;
  padding: 20px 10px;
  display: flex;
}

.codes__cells {
  grid-template-columns: repeat(6, 75px);
  gap: 15px;
  margin-bottom: 30px;
  display: grid;
}

.codes__cell {
  width: auto;
  display: block;
  position: relative;
}

.codes__cell input {
  border-radius: var(--radius-main);
  width: 100%;
  text-align: center;
  padding: 17px;
  position: relative;
}

.codes__cell-label {
  border: 1px solid var(--color-gray-100);
  border-radius: var(--radius-main);
  width: 100%;
  height: 100%;
  font-size: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.codes__cell-label:before {
  content: "";
  -webkit-mask-composite: destination-out;
  border-radius: var(--radius-btn);
  background: var(--color-gradient-main) border-box;
  border-radius: var(--radius-main);
  opacity: 0;
  border: 1px solid #0000;
  position: absolute;
  inset: 0;
  -webkit-mask-image: linear-gradient(#fff 0 0), linear-gradient(#fff 0 0);
  mask-image: linear-gradient(#fff 0 0), linear-gradient(#fff 0 0);
  -webkit-mask-position: 0 0, 0 0;
  mask-position: 0 0, 0 0;
  -webkit-mask-size: auto, auto;
  mask-size: auto, auto;
  -webkit-mask-repeat: repeat, repeat;
  mask-repeat: repeat, repeat;
  -webkit-mask-clip: padding-box, border-box;
  mask-clip: padding-box, border-box;
  -webkit-mask-origin: padding-box, border-box;
  mask-origin: padding-box, border-box;
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  -webkit-mask-source-type: auto, auto;
  mask-mode: match-source, match-source;
